<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-04 10:32:52
 * @LastEditTime: 2019-10-21 15:56:32
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <p class="title">{{data.displayed_owned_by}}</p>
    <div class="headerimgs progressive">
      <!-- v-progressive="data.progress_logo" -->
      <img
        class="headerimg"
        v-progressive="data.progress_logo"
        src="@/assets/images/loading photo@3x.png"
        alt
      />
    </div>

    <p class="travelead">{{data.name}}</p>

    <p class="budget">{{data.desc1}}</p>

    <div class="travel-insurance">{{data.category_name}}</div>

    <!-- 价格 -->
    <div class="price" v-if="plan">
      <!-- 分享按钮和点赞按钮 -->
      <!-- <div class="fontIcon">
        <i class="iconfont icon-shoucang"></i>
        <i class="iconfont icon-fenxiang"></i>
      </div>-->
      {{plan.price_desc ? plan.price_desc : ''}}
    </div>

    <div class="projectImg progressive">
      <!-- v-progressive="data.products_imgs[0] -->
      <img
        class="box"
        v-progressive="data.products_imgs[0]"
        src="@/assets/images/loading photo@1X.png"
        alt
      />
    </div>
    <div class="line"></div>
    <!-- Single Plan和Annual Plan -->
    <div class="buttons">
      <button
        class="single btn"
        v-for="(item,index) in this.data.plan_list"
        :key="index"
        @click="setPlanProductId(item)"
        :class="{isActive:plan.period_type == item.period_type}"
      >{{item.plan_list_name}}</button>
    </div>

    <div class="trip" v-if="plan">{{plan.plan_list_desc ? plan.plan_list_desc : ''}}</div>

    <!-- Key Features -->
    <div>
      <p class="key">{{$t('product_decs_key_title')}}</p>
      <div class="key-content">
        <div class="features-wrap">
          <p :class="{'features': flag}">{{data.remarks}}</p>
          <span class="more" @click="clickmore">{{flag ? $t('product_decs_key_more') : $t('hide')}}</span>
        </div>
      </div>
    </div>

    <!-- Customer Service -->
    <div class="customers" @click="customers">
      <span class="starrs">
        <img class="starr" :src="data.cs_logo" alt />
      </span>
      <span class="customer">{{$t('account_customer_service')}}</span>
      <i class="cubeic-arrow"></i>
    </div>

    <!-- Coverage Details -->
    <a
      class="coverages"
      target="_Blank"
      :href="'https://dev-wesurance814.com/wesupromo/webview/coverageDetails?product_id='+productId+'&language='+Language"
    >
      <span class="covers">
        <img class="cover" src="@/assets/images/Confirmation of cover@3x.png" alt />
      </span>
      <span class="coverage">{{$t('products_choose_plan_coverage_title1')}}</span>
      <i class="cubeic-arrow"></i>
    </a>

    <div class="button-bar">
      <button class="quato" @click="quato">{{$t('Quote')}}</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      travelInfo: {},
      more: false,
      data: "",
      flag: true,
      remarks: [],
      ischecked: 0,
      productId: "",
      areacode: "86",
      trip: "",
      price: "",
      Language: "",
      rendomNum: "",
      MerchantId: "" //商務id//GOLIONHK
    };
  },
  computed: {
    ...mapState(["product_id", "language", "plan"])
  },
  methods: {
    ...mapMutations([
      "setPlan",
      "setName",
      "reset",
      "setProductId",
      "setLanguage",
      "setSession",
      "setProductType",
      "setMerchantId",
      "setPriceSymbol",
      "setages"
    ]),
    clickmore() {
      this.flag = !this.flag;
    },
    /* isChoose(item) {
        this.setPlan(item)
        this.ischecked = item.period_type;
        this.trip=item.plan_list_desc;
        this.price=item.price_desc;
        console.log(this.ischecked)
    }, */
    customers() {
      this.$createDialog({
        type: "alert",
        confirmBtn: { text: "確認", active: true, href: "javascript:;" },
        content: this.data.customer_service

        // '如有查询，请联络Starr:<a href="mailto:(852)37655566/cs.hk@starrcompanies.com">(852)37655566/cs.hk@starrcompanies.com</a>(办公时间：星期一至星期五，上午9:00至12:30；下午1:30至下午5:00)',
      }).show();
    },
    // coverage() {
    //   //this.$router.push({ path: "/CoverageDetails" });
    //   //window.location.href="http://njrsgn.natappfree.cc/wesuapi4/webview/coverageDetails?product_id=5d70aec247e5a01196e929b3&language=tc"
    // },
    setPlanProductId(item) {
      this.setPlan(item);
      this.setProductId(item.product_id);
    },
    quato() {
      this.$router.push({ path: "/Travelinfo" });
    },
    //生成8位随机数,调接口把数据传给后端
    getRamNumber() {
      var result = "";
      for (var i = 0; i < 8; i++) {
        result += Math.floor(Math.random() * 16).toString(16); //获取0-15并通过toString转16进制
      }
      //默认字母小写，手动转大写
      console.log(result.toUpperCase()); //另toLowerCase()转小写
      this.rendomNum = result.toUpperCase();
    }
  },
  created() {
    const resetFile = [
      "product_info",
      "plan",
      "travelInfo",
      "userInfo",
      "protectorInfo",
      "userPhone",
      "userId",
      "commonKey",
      "certificateId"
    ];
    this.reset(resetFile);
    //调用生成随机数的方法
    this.getRamNumber();
    //获取url中的产品id和语言,将产品id和语言保存到vuex中
    console.log(this.$route.params.productId);
    console.log(this.$route.params.language);
    console.log(this.$route.params.merchantId);
    this.productId = this.$route.params.productId;
    this.Language = this.$route.params.language;
    this.MerchantId = this.$route.params.merchantId;
    this.setProductId(this.productId);
    this.setLanguage(this.Language);
    this.setMerchantId(this.MerchantId);
    //保存session-id// this.rendomNum
    this.setSession(this.MerchantId + this.rendomNum); //merchant_id暫時寫死
    window.localStorage.setItem("lang", this.Language);
    console.log(this.$i18n.locale);
    //点击按钮用一下代码可以更换语言
    this.$i18n.locale =
      this.Language == "tc"
        ? (this.$i18n.locale = "tc")
        : (this.$i18n.locale = "eng");
  },
  mounted() {
    console.log(this.language);
    var params = {
      language: this.Language,
      product_id: this.productId,
      phone_country_code: this.areacode,
      merchant_id: this.MerchantId
    };
    const loading = this.$createToast({
      time: 0,
      txt: "Loading..."
    });
    //显示加载动画
    loading.show();
    this.$http
      .post("/wesupromo/web/tpc/getProductDetails", params)
      .then(res => {
        if (res.status == 100) {
          this.data = res.data;
          console.log(this.data);
          //数据加载完关闭加载动画
          this.$nextTick(() => {
            loading.hide();
          });
          this.setPlan(res.data.plan_list[0]);
          this.setName(res.data.name);
          this.setProductType(res.data.product_type);
          this.setPriceSymbol(res.data.currency_desc);
          this.setages({
            max_age: this.data.max_age,
            max_persons: this.data.max_persons,
            max_pp_age: this.data.max_pp_age,
            min_age: this.data.min_age,
            min_pp_age: this.data.min_pp_age
          });
          
        }
      });
    //调用储存点击量接口
    this.$http
      .post("/wesupromo/web/addClicks", {
        trx_id: this.MerchantId + this.rendomNum,
        merchant_id: this.MerchantId,
        product_id: this.productId
      })
      .then(res => {
        console.log(res);
      });
    // 函数中的参数为 要获取的cookie键的名称。
    function getCookie(c_name) {
      let c_start = "";
      let c_end = "";
      if (document.cookie.length > 0) {
        c_start = document.cookie.indexOf(c_name + "=");
        if (c_start != -1) {
          c_start = c_start + c_name.length + 1;
          c_end = document.cookie.indexOf(";", c_start);
          if (c_end == -1) {
            c_end = document.cookie.length;
          }
          return unescape(document.cookie.substring(c_start, c_end));
        }
      }
      return "";
    }
    var uname = getCookie("Name");
    console.log(uname);
    //如果cookie中没有name就设置一个并且调用接口
    if (uname == "") {
      //调用储存点击量接口
      this.$http
        .post("/wesupromo/web/addClicks", {
          //merchant_id暫時寫死
          trx_id: this.MerchantId + this.rendomNum,
          merchant_id: this.MerchantId,
          product_id: this.productId
        })
        .then(res => {
          console.log(res);
          //设置cookie
          // 如果要设置过期时间以秒为单位
          function setCookie(c_name, value, expireseconds) {
            var exdate = new Date();
            exdate.setTime(exdate.getTime() + expireseconds * 1000);
            document.cookie =
              c_name +
              "=" +
              escape(value) +
              (expireseconds == null ? "" : ";expires=" + exdate.toGMTString());
          }
          setCookie("Name", "Golion", 30); //cookie过期时间为一个30秒
        })
        .catch(error => {
          console.log(error);
        });
      console.log("cookie过期了");
    }
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  padding-bottom: 260px;
  background-color: #fff;
  position: relative;
  .title {
    color: rgb(155, 155, 155);
    font-size: 25px;
  }
  .travelead {
    color: #000;
    font-weight: 700;
    font-size: 44px;
    line-height: 52px;
    margin-bottom: 15px;
  }
  .budget {
    color: #000;
    font-weight: 600;
    font-size: 34px;
    line-height: 42px;
    margin-bottom: 10px;
  }
  .travel-insurance {
    color: rgb(142, 142, 147);
    font-size: 30px;
  }
  .headerimgs {
    width: 192px;
    height: 192px;
    img {
      width: 100%;
      height: 100%;
    }
  }

  .price {
    text-align: right;
    color: rgb(12, 129, 255);
    font-size: 35px;
    font-weight: bold;
    margin-bottom: 15px;
    margin-top: 35px;
    position: relative;
    height: 60px;
    .fontIcon {
      display: inline-block;
      text-align: left;
      position: absolute;
      left: 0;
      .iconfont {
        font-size: 35px;
        color: rgb(151, 151, 151);
        margin-left: 15px;
      }
    }
  }
  .projectImg {
    width: 100%;
    border-radius: 22px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .line {
    width: 100%;
    height: 5px;
    background-color: rgb(233, 233, 233);
    margin-top: 20px;
  }

  .cubeic-arrow {
    color: rgba(0, 0, 0, 0.25);
    font-size: 32px;
    font-weight: bold;
  }
  .buttons {
    line-height: 100px;
    display: flex;
    justify-content: space-around;
    margin: 12px 0 18px 0;
  }

  .btn {
    background: rgb(224, 223, 223);
    touch-action: none;
    width: 260px;
    height: 65px;
    border-radius: 35px;
    color: rgb(177, 175, 175);
    font-weight: 550;
    font-size: 30px;
    outline: none;
    border: none;
  }
  .isActive {
    background: rgb(12, 129, 255);
    color: white;
  }

  .trip {
    color: rgb(12, 129, 255);
    font-weight: 500;
    border-bottom: 1px solid rgb(209, 208, 208);
    line-height: 65px;
  }
  .key {
    font-size: 34px;
    font-weight: 550;
    line-height: 100px;
  }
  .key-content {
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 15px;
    p {
      font-size: 30px;
      font-family: Arial, Helvetica, sans-serif;
      line-height: 45px;
    }
  }
  .features-wrap {
    white-space: pre-line;
    position: relative;
    .more {
      display: block;
      width: 100%;
      text-align: right;
      color: rgb(12, 129, 255);
    }
  }
  .features {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    /* autoprefixer:off */
    -webkit-box-orient: vertical;
    /* autoprefixer:on */
    -webkit-line-clamp: 3;
  }
  .customers {
    line-height: 75px;
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    .starrs {
      display: inline-block;
      width: 65px;
      height: 65px;

      .starr {
        width: 100%;
        height: 100%;
        vertical-align: middle;
        margin-left: 20px;
      }
    }
    .customer {
      font-size: 35px;
      font-family: Arial, Helvetica, sans-serif;
      margin-left: -220px;
    }
  }
  .coverages {
    line-height: 75px;
    padding-bottom: 15px;
    display: flex;
    color: #000;
    justify-content: space-between;
    .covers {
      display: inline-block;
      width: 65px;
      height: 65px;
      .cover {
        width: 100%;
        height: 100%;
        vertical-align: middle;
        margin-left: 20px;
      }
    }
    .coverage {
      font-size: 35px;
      font-family: Arial, Helvetica, sans-serif;
      margin-left: -220px;
    }
  }

  .button-bar {
    text-align: center;
    position: fixed;
    width: 100%;
    height: 200px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(250, 250, 250);
  }
  .quato {
    background: rgb(239, 69, 95);
    width: 350px;
    height: 100px;
    margin-top: 50px;
    border-radius: 15px;
    color: white;
    font-weight: 550;
    font-size: 35px;
    outline: none;
    border: none;
  }
}
</style>


